
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
.child-view{transition:all 0.5s cubic-bezier(0.55, 0, 0.1, 1)
}
.slide-left-enter,.slide-right-leave-active{opacity:0;transform:translate(30px, 0)
}
.slide-left-leave-active,.slide-right-enter{opacity:0;transform:translate(-30px, 0)
}



